import React from 'react'

export default class SnapFunc extends React.Component {
   state = {
       Snap: null
   }
    componentDidMount() {
        this.setState({Snap: require('snapsvg')})
    }
    
   
    Snap = () => {
        const StateSnap = this.state.Snap()
    return StateSnap()
    }
    
}
